import styled from 'styled-components/macro';

export const Container = styled.div`

    display: flex;
    justify-content: center;
    height: 100%;
    margin-top: 20px;
    flex-wrap: wrap;

    @media (max-width: 1000px) {
        flex-direction: column;
        align-items: center;
    }

`;

export const Input = styled.input`

    max-width: 450px;
    width: 100%;
    border: 0;
    padding: 5px;
    height: 70px;
    box-size: border-box;
    align: center;

    @media (max-width: 1000px) {
        height: 40px;
    }
    
    @media (max-width: 400px) {
        height: 30px;
    }

`;

export const Button = styled.button`

    display: flex;
    color: white;
    align-items: center;
    height: 80px;
    background: #e50914;
    text-transform: uppercase;
    padding: 0 32px;
    font-size: 26px;
    border: 0;
    cursor: pointer;

    &:hover {
        background: #112D37;
    }    

    @media (max-width: 1000px) {
        height: 50px;
        font-size: 16px;
        margin-top: 20px;
        font-weight: bold;
    }

    @media (max-width: 600px) {
        height: 40px;
        font-size: 16px;
        margin-top: 20px;
        font-weight: bold;
    }

    img{
        margin-left: 10px;
        filter: brightness(0) invert(1);
        width: 24px;

        @media (max-width: 1000px) {
            width: 16px;
        }
    }

`;

export const Text = styled.p`

    font-size: 19.2px;
    color: white;
    flex-direction: column;
    text-align: center;

    @media (max-width: 1000px) {
        font-size: 16px;
        line-height: 22px;
    }

`;

export const Break = styled.div`

    flex-basis: 100%;
    height: 0;

`;



